import React from 'react';
import 'react-datepicker/dist/react-datepicker.css';
import { connect } from 'react-redux';

import { setCompanyData } from '../../../../reduxStore/actions/flowActions';
import { Button, ModalR } from '../../../common';
import cls from './InfoTaskModal.module.scss';
import moment from 'moment';
import { isMobile } from 'react-device-detect';

function InfoTaskModal(props) {
	const { modalVisible, childrenWrapperStyle, modalClosed, openedTask } = props;
	const { name, created_at, completed_on, duration, process, creator, assignee, components } = openedTask;
	return modalVisible ? (
		<div>
			<ModalR backdropStyle={{ height: '900px' }} marginTop={'500px'} onClose={modalClosed} shadowedContainerStyle={{ width: isMobile ? '100%' : '40%' }} position={'fixed'} scrollable={true} backdropColor={true} blured={true} width={'90%'} isOpen={modalVisible}>
				<div className={cls.backIconContainer}>
					<Button title={'Close'} clicked={modalClosed} />
				</div>
				<div className={cls.contentWrapper} style={{ ...childrenWrapperStyle }}>
					<div className={cls.contentContainer}>
						<div className={cls.content}>
							<FieldHistory title={'Task Name'} content={name} />
							<FieldHistory title={'Task Created On'} content={moment(created_at).format('DD of MMMM YYYY hh:mm')} />
							<FieldHistory title={'Completed On'} content={moment(completed_on).format('DD of MMMM YYYY hh:mm')} />
							<FieldHistory title={'Time on this Task'} content={duration} />
							<FieldHistory title={'Task Creator'} content={creator.first_name + ' ' + creator.last_name} />
							<FieldHistory title={'Assignee'} content={assignee.first_name + ' ' + assignee.last_name} />
						</div>
					</div>
				</div>
			</ModalR>
		</div>
	) : null;
}

const FieldHistory = props => {
	const { title, content } = props;
	return (
		<div>
			<p className={cls.fieldHistoryTitle}>{title}</p>
			<p className={cls.savedDescription}>{content}</p>
			{props.children}
		</div>
	);
};

const mapStateToProps = store => {
	const { inviteColleguesList, companyData } = store.flowReducer;
	return { inviteColleguesList, companyData };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetCompanyDataField: field => dispatch(setCompanyData(field)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(InfoTaskModal);
