import { TaskBoardPanel, ModalR, Spinner, Button } from '../../common';
import { uncachedUrl } from '../../../shared/utility';
import cls from './UserOverviewModal.module.scss';
import PageSubtitle from '../../common/PageSubtitle/PageSubtitle';
import React, { useCallback, useEffect, useState } from 'react';
import moment from 'moment';
import { getAllCompletedTasks, getAllTasks } from '../../../services/tasks.service';
import { first, take } from 'rxjs/operators';
import TaskCard from '../TaskComps/TaskCard/TaskCard';
import TasksTable from '../TaskComps/TasksTable/TasksTable';
import { useNavigate } from 'react-router-dom';
import { isMobile } from 'react-device-detect';

const UserOverviewModal = props => {
	const { isVisible, activeItem, handleClose, isAdmin } = props;
	const { first_name, last_name, id } = activeItem;
	const [tasks, setTasks] = useState([]);
	const [allCompletedTasks, setAllCompletedTasks] = useState([]);
	const [renderCompletedTasks, setRenderCompletedTasks] = useState([]);
	const [loading, setLoading] = useState(false);
	const [loadingHistory, setLoadingHistory] = useState(false);
	const [renderTasks, setRenderTasks] = useState({ laterTasks: [], todayTasks: [], overdueTasks: [] });
	const [page, setPage] = useState(1);
	const { current_page, from } = allCompletedTasks?.meta || {};
	const historyData = allCompletedTasks.data && allCompletedTasks?.data?.map(elem => elem);
	const settingLoading = val => setLoading(val);
	const settingLoadingHistory = val => setLoadingHistory(val);
	const navigate = useNavigate();
	useEffect(() => setCompletedTasks(), [allCompletedTasks.data]);
	useEffect(() => setTaskArray(), [tasks]);
	useEffect(() => {
		settingLoading(true);
		getAllTasks(id, true)
			.pipe(take(1))
			.subscribe({
				next: data => {
					setTasks(data);
					settingLoading(false);
				},
				error: () => {
					settingLoading(false);
				},
				complete: () => {
					settingLoading(false);
				},
			});
	}, []);
	useEffect(() => {
		settingLoadingHistory(true);
		getAllCompletedTasks(page, id, true)
			.pipe(first())
			.subscribe({
				next: data => {
					setAllCompletedTasks(data);
					settingLoadingHistory(false);
				},
			});
	}, [page]);

	const setTaskArray = useCallback(() => {
		const overdue = [],
			today = [],
			later = [];
		if (tasks?.length === 0) return;
		tasks.map(e => {
			const todayDate = moment(new Date()).format('YYYY-MM-DD');
			if (moment(e.due_date, 'YYYY-MM-DD').isSame(todayDate)) {
				return today.push(e);
			} else if (moment(e.due_date, 'YYYY-MM-DD').isAfter(todayDate)) {
				return later.push(e);
			} else {
				return overdue.push(e);
			}
		});
		const renderObjects = { laterTasks: later, todayTasks: today, overdueTasks: overdue };
		setRenderTasks(renderObjects);
	}, [tasks]);

	let renderTasksFunction = tasks => {
		return (
			tasks.length > 0 &&
			tasks.map((task, index) => {
				return (
					<div key={index}>
						<TaskCard task={task} taskId={task.id} />
					</div>
				);
			})
		);
	};

	const columns = React.useMemo(
		() => [
			{
				Header: () => {
					return <span>Task Name</span>;
				},
				accessor: 'taskName',
			},

			{
				Header: () => {
					return <span>Completed On</span>;
				},
				accessor: 'completedOn',
			},
		],
		[]
	);

	const setCompletedTasks = useCallback(() => {
		const completed = [];
		if (allCompletedTasks?.data?.length === 0) return;

		allCompletedTasks?.data?.map(elem => {
			return completed.push({
				taskName: elem.name,
				process: elem?.process?.name,
				completedOn: moment(elem.completed_at).format('DD of MMMM YYYY hh:mm'),
				duration: elem.duration,
			});
		});
		setRenderCompletedTasks(completed);
	}, [allCompletedTasks]);

	return (
		<ModalR isOpen={isVisible} modalWrapperStyle={{ margin: 0 }} shadowedContainerStyle={{ width: isMobile ? '100%' : '50%' }} onClose={() => handleClose()} scrollable={true} blured={true}>
			<div style={{ display: 'flex', justifyContent: 'space-between' }}>
				<Button title={'Close'} clicked={handleClose} style={{ width: '100px' }} />
			</div>
			<div style={{ padding: '20px' }}>
				<div style={{ display: 'flex', flexDirection: 'column' }}>
					<div className={cls.topContainer}>
						<div className={cls.avatarContainer}>{activeItem?.photo?.length > 0 && <img alt="lg" src={uncachedUrl(activeItem.photo)} />}</div>
					</div>
					<div style={{ padding: '30px' }}>
						<p className={cls.name}>{first_name + ' ' + last_name}</p>
						{activeItem?.positions?.length > 0 &&
							activeItem?.positions?.map((item, index) => {
								return (
									<div key={index}>
										<p className={cls.position}>{item?.name}</p>
									</div>
								);
							})}
					</div>
				</div>

				
				<div style={{ display: 'flex', flexDirection: 'column' }}>
					<div
						style={{
							display: 'flex',
							flexDirection: 'row',
							alignItems: 'center',
							padding: '5px',
							paddingLeft: '5px',
						}}
					>
						<PageSubtitle subtitleText={'Tasks History'} />
					</div>
					{loadingHistory ? (
						<Spinner type={'BallTriangle'} color="var(--app-color-gray-light)" height={30} width={`${30}px`} />
					) : (
						<TasksTable loading={loadingHistory} currentPage={current_page} lastPage={from} setLoading={setLoadingHistory} historyData={historyData} page={page} setPage={setPage} columns={columns} data={renderCompletedTasks} />
					)}
				</div>
			</div>
		</ModalR>
	);
};

export default UserOverviewModal;
