import React from 'react';
import cls from './PageSubtitle.module.scss';

const PageSubtitle = props => {
	const { subtitleImage, subtitleText, style, smallerSubtitle } = props;
	return (
		<div className={cls.host}>
			<div className={cls.pageSubtitleHost} style={{ ...style }}>
				{subtitleImage && <img src={subtitleImage} className="grey-svg-image-filter" alt="" />}
				<p>{subtitleText}</p>
			</div>
			{smallerSubtitle && (
				<div className={cls.pageSubtitleChild}>
					<p>{smallerSubtitle}</p>
				</div>
			)}
		</div>
	);
};

export default PageSubtitle;
