import React, { useState } from 'react';
import { useTable } from 'react-table';
import { LoadMoreButton } from '../../../common';
import InfoTaskModal from '../InfoTaskModal/InfoTaskModal';
import cls from './TasksTable.module.scss';

function TasksTable(props) {
	const { historyData, currentPage, lastPage, setPage, loading, setLoading, columns, data } = props;
	const [visible, setVisible] = useState(false);
	const [taskName, setTaskName] = useState('');
	const [createdAt, setCreatedAt] = useState('');
	const [completedOn, setCompletedOn] = useState('');
	const [duration, setDuration] = useState('');
	const [taskCreator, setTaskCreator] = useState('');
	const [taskAssignee, setTaskAssignee] = useState('');
	const [process, setProcess] = useState('');
	const [openedTask, setOpenedTask] = useState('');

	const loadMore = () => {
		setLoading(!loading);
		setPage(currentPage + 1);
	};
	const openInfoModal = values => {
		setOpenedTask(values);
		setVisible(!visible);
		setTaskName(values.taskName);
		setCreatedAt(values.createdAt);
		setCompletedOn(values.completedOn);
		setDuration(values.duration);
		setTaskCreator(values.taskCreator);
		setTaskAssignee(values.taskAssignee);
		setProcess(values?.process);
	};

	const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({
		columns,
		data,
	});

	return (
		<div className={cls.tasksTableHost}>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map((headerGroup, idx) => (
						<tr key={idx} {...headerGroup.getHeaderGroupProps()} className={'header-row'}>
							{headerGroup.headers.map((column, index) => (
								<th key={index} {...column.getHeaderProps()}>
									{column.render('Header')}
								</th>
							))}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{rows.map((row, i) => {
						prepareRow(row);
						return (
							<tr key={i} {...row.getRowProps()} onClick={() => openInfoModal(historyData[i])} style={{ cursor: 'pointer' }}>
								{row.cells.map((cell, idx) => {
									return (
										<td key={idx} {...cell.getCellProps()}>
											{cell.render('Cell')}
										</td>
									);
								})}
							</tr>
						);
					})}
				</tbody>
			</table>
			{data.length == 0 && <p>{'No Tasks Found'}</p>}
			<div style={{ textAlign: 'center', marginTop: '20px', paddingBottom: '50px' }}>{currentPage !== lastPage && data.length > 0 && <LoadMoreButton loadMorePressed={loadMore} apiRequest={loading} />}</div>

			<InfoTaskModal
				openedTask={openedTask}
				createdAt={createdAt}
				taskName={taskName}
				completedOn={completedOn}
				duration={duration}
				taskCreator={taskCreator}
				taskAssignee={taskAssignee}
				process={process}
				modalVisible={visible}
				modalClosed={() => setVisible(!visible)}
				hasTutorial={true}
				blured={true}
				childrenWrapperStyle={{ alignItems: 'center' }}
				onCancel={() => setVisible(!visible)}
			/>
		</div>
	);
}

export default TasksTable;
